<template>
       <v-container class="container_principal">
           <h2  v-if="$vssWidth >= 650"  class='titleContac'>
                    CONTÁCTANOS
                </h2>
           <v-container class="container_contacto">
                 <v-container
                    class='cardCont'>
                    <p class="datosT" > Deja tus datos</p>
                    <v-row>
                        <v-col cols='12' md= '6' class="paddingT">
                            <v-text-field
                                outlined
                                dense
                                v-model="nombre"
                                :required= rules.required
                                :rules="[rules.required, rules.min, rules.text]"
                                label="Nombre *"
                                hide-details="auto"
                            ></v-text-field>
                        </v-col>
                        <v-col cols='12' md= '6' class="paddingT">
                            <v-text-field
                                outlined
                                dense
                                v-model="telefono"
                                :rules="[rules.required, rules.minNu, rules.number]"
                                label="Teléfono *"
                                hide-details="auto"
                            ></v-text-field>
                        </v-col>
                        <v-col cols='12' md= '12' class="paddingT">
                        <v-text-field
                                outlined
                                v-model="correo"
                                :rules="[rules.email]"
                                label="Correo electrónico"
                                hide-details="auto"
                            ></v-text-field>
                        </v-col>
                        <v-col cols='12' md= '12' class="paddingT">
                            <v-textarea
                                outlined
                                dense
                                rows="2"
                                v-model="necesidad"
                                label="Cúentanos tus necesidades..."
                                hide-details="auto"
                            ></v-textarea>
                        </v-col>
                        <v-container v-bind:style="{'text-align': 'end'}" >
                            <v-btn
                                dark
                                @click="enviar"
                                color='#008dff'
                                class="btnContacto"
                            >
                            Enviar
                            </v-btn> 
                        </v-container>
                    </v-row>
                </v-container>
                <v-container class="cardPic" v-if="$vssWidth >= 650" >
                        <v-col class="down" >
                            <v-row>
                                <v-icon
                                class="contact_icon"
                                color="white">
                                    mdi-email
                                </v-icon>
                                <h4 class="conteCall m"> Escríbenos</h4>
                            </v-row>
                                <h4 class="conteCall">     soporte@capitalbranch.org</h4>
                        </v-col>
                        <v-col>
                            <v-row>
                                <v-icon
                                class="contact_icon"
                                    color="white">
                                    mdi-phone
                                </v-icon>
                                <h4 class="conteCall m"> Llámanos</h4>
                            </v-row>
                                <h4 class="conteCall">  315 584 6857 - 312 468 4588</h4>
                        </v-col>
                    </v-container>
             </v-container>
       </v-container>
</template>

<script>
import { host} from './Tools/variables'
import axios from 'axios'
export default {
    data() {
        return {
            necesidad: null, 
            telefono: null, 
            nombre: null, 
            correo: null, 
            rules: {
                required: value => 
                     !!value || 'Campo requerido.',
                min: v => { 
                    v.length > 2 || 'Invalido'},
                minNu: v => { 
                    v.length > 5 || 'Invalido'},
                text: v => {
                    const req = /^[\w'\-,.][^0-9_!¡?÷?¿/\\+=@#$%ˆ&*(){}|~<>;:[\]]{2,}$/
                    return req.test(v) || 'Solo letras'
                },
                number: v => {
                    const req = /^[0-9 ]*$/
                    return req.test(v) || 'solo numeros'
                },
                email: v =>{
                    const req = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
                    return req.test(v) || 'formato invalido para el correo'
                }
            },
        }
    },
    methods: {
        enviar(){
            if(this.nombre == null || this.telefono == null){
                this.$alert('Debes agregar al menos tu nombre y un teléfono')
            }else{
                const fd = new FormData();
                    fd.append('mail', this.correo);
                    fd.append('phone', this.telefono);
                    fd.append('text', this.necesidad);
                    fd.append('name', this.nombre);
                    axios.post(host, fd).then((result) => {                
                    let data = result.data.mensaje;
                        if(data == 'OK'){
                            this.$alert('Hemos enviado tu solicitud, te estaremos contactando lo antes posible')  
                            this.nombre = null
                            this.telefono = null
                            this.necesidad = null
                            this.correo = null  
                        }
                        else{
                            this.$$alert('Error!, intenta enviar la solicitud nuevamente')
                        }
                }) 
            }
        }  
    },

}
</script>

