<template>
  <v-container class="fondo" id="fondo">
    <template v-if="$vssWidth >= 650" id="filter">
      <tool-bar :tab="tab" v-on:eventosPrincipales="eventos($event)">
      </tool-bar>
    </template>
    <template v-if="$vssWidth <= 650" id="filterS">
      <menu-bar></menu-bar>
    </template>
    <v-content class="contentPage"  >
        <router-view v-on:eventosPrincipales="eventos($event)"></router-view>
    </v-content >
    <footer-bar></footer-bar>
  </v-container>
</template>

<script>
import ToolBar from "./ToolBarWeb.vue";
import MenuBar from "./menuBarMovile.vue";
import FooterBar from "./FooterBar.vue"

export default {
  name: "Esquema",
  components: {
    ToolBar,
    FooterBar,
    MenuBar
  },
  data() {
    return {
      route: null
    };
  },
  mounted() {
    this.route = this.$route.name;
    this.changeImg();
  },
  updated() {
    this.route = this.$route.name;
    this.changeImg();
  },
  methods: {
    changeImg() {
      if (this.$vssWidth <= 650) {
        document.getElementById("fondo").style.backgroundImage = "none";
      } else {
        document.getElementById("fondo").style.backgroundRepeat = "no-repeat";
        document.getElementById("fondo").style.backgroundSize = "100%";
        if (this.route == "Inicio") {
          this.tab = 0;
          document.getElementById("fondo").style.backgroundImage =
            "url(" + require("../assets/fondo1.jpg") + ")";
        }
      }
    },
  eventos(a){
    switch(a){
      case 'productos':
        this.tab = 2;
        document.getElementById("fondo").style.backgroundImage =
        "url(" + require("../assets/fondoProductos.jpg") + ")";
      break;
      case 'contacto':
        this.tab = 3;
        document.getElementById("fondo").style.backgroundImage =
        "url(" + require("../assets/fondoContacto.jpg") + ")";
      break;
      case 'nosotros':
        this.tab = 1;
        document.getElementById("fondo").style.backgroundImage =
        "url(" + require("../assets/fondoSomos.jpg") + ")";
        break;
      case 'QuienesSomos':
        document.getElementById("fondo").style.backgroundImage =
        "url(" + require("../assets/fondoSomos.jpg") + ")";
      break;
      case 'QueHemosHecho':
        document.getElementById("fondo").style.backgroundImage =
        "url(" + require("../assets/fondoNosotros.jpg") + ")";
      break;
      case 'Aliados' :
        document.getElementById("fondo").style.backgroundImage =
        "url(" + require("../assets/fondoEquipo.jpg") + ")";
      break;
      default:
        break;
    }
    }
  }
};
</script>

